import vm from '../main'
const $ = {
	clientHeight:function(){
		return window.innerHeight
	},
	/**
	 * 将id、parentId转换成children格式
	 */
	transformTree:function(data){
		let res = data.filter((val)=>{
			return !val.parentId||val.parentId=="0"
		})
		function build(obj){
			for(let x of data){
				if(x.parentId == obj.id){
					obj.children?obj.children:obj.children=[]
					obj.children.push(x)
					build(x)
				}
			}
		}
		for(let x of res){
			build(x)
		}

		//排序
		function deepSort(arr){
			function sort(a,b){
				return a.itemOrder - b.itemOrder
			}
			arr.sort(sort)
			for(let x of arr){
				if(x.subMenus){
					deepSort(x.subMenus)
				}
				else if(x.children){
					deepSort(x.children)
				}
			}
		}
		deepSort(res)
		return res
	},
	/**
	 * 将children转换成id、parentId格式
	 */
	transformArray:function(arr,key){
		let res = [];
		function build(arr){
			for(let x of arr){
				res.push(x)
				if(x[key]){
					build(x[key])
				}
			}
		}
		build(arr)
		return res
	},
	shallowCopy:function (src) {
	  var dst = {};
	  for (var prop in src) {
	    if (src.hasOwnProperty(prop)) {
	      dst[prop] = src[prop];
	    }
	  }
	  return dst;
	}
}
$.delete = function(tips,callback){
	vm.$confirm(tips, '提示', {
		confirmButtonText: '确定',
		cancelButtonText: '取消',
		type: 'warning'
	}).then(() => {
		callback()
	}).catch(() => {
		vm.$message({
			type: 'info',
			message: '已取消移除'
		});
	});
}


window._parse = function(data){
	console.log(JSON.parse(JSON.stringify(data)))
}
export default $



