<template>
	<div>
		<el-table :data="tableData"
			 @selection-change="selectChange"
			stripe 
			style="width: 100%;height:100%;" 
			:max-height="height" 
			v-loading="tableLoading" 
			element-loading-text="拼命加载中">
			<el-table-column v-if="showCheckBox" type="selection" width="50"></el-table-column>
			<el-table-column type="index" label="序号" width="80"></el-table-column>
			<el-table-column v-for="item in tableOption" :key="item.id" :prop="item.prop" :label="item.label" :width="item.width"></el-table-column>
			<slot name="prop1"></slot>
			<slot name="operationTool"></slot>
		</el-table>
		<el-pagination
			@size-change="sizeChange"
			@current-change="pageChange"
			:page-size="pageSize"
			:page-sizes="[5, 10, 20, 50, 100]"
			layout="sizes,total, prev, pager, next"
			:total="total">
		</el-pagination>
	</div>
</template>

<script>
	export default {
		props:['tableOption','tableData','tableLoading','total','pageSize','pageChange','sizeChange','showCheckBox','maxHeight'],
		data() {
			return {
				height : this.maxHeight||this.$.clientHeight()-260
			};
		},
		methods:{
			selectChange(arr){
				let res = arr.map((val)=>{
					return val.id
				})
				this.$emit("selectChange",res)
			}
		}
	};
</script>


<style scoped>
  .el-pagination{
    margin-top: 5px;
    float: right;
  }
</style>



