<template>
	<el-dialog title="新建API" :visible.sync="show" size="tiny">
		<el-form :model="form" :rules="rules" ref="form">
			<el-form-item label="名称" prop="name">
				 <el-autocomplete
				 	class="block"
			      v-model="form.name"
			      :fetch-suggestions="querySearch"
			      placeholder="名称">
			      </el-autocomplete>
			</el-form-item>
			<el-form-item label="url地址" prop="url">
				<el-input v-model="form.url" placeholder="地址">
					<template slot="prepend">http://127.0.0.1:5086/mes/rest</template>
				</el-input>
			</el-form-item>
			<el-form-item label="类型" prop="type">
				<el-select v-model="form.type" placeholder="类型">
					<el-option label="GET" value="GET"></el-option>
					<el-option label="POST" value="POST"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="默认参数" prop="param">
				<el-input type="textarea" v-model="form.param" placeholder="默认参数"></el-input>
			</el-form-item>
		</el-form>
		<span slot="footer" class="dialog-footer">
		    <el-button @click="show = false">取 消</el-button>
		    <el-button type="primary" @click="submit()">确 定</el-button>
		  </span>
	</el-dialog>
</template>

<script>
	import {
		create
	} from "./service";

	class Form {
		constructor() {
			this.name = "";
			this.url = "";
			this.type = "";
			this.param = "";
		}
	}
	export default {
		data() {
			const param = (rule, value, callback) => {
				if(value) {
					function isJSON (str) {
					  str = str.replace(/\s/g, '').replace(/\n|\r/, '');
					  if (/^\{(.*?)\}$/.test(str))
					    return /"(.*?)":(.*?)/g.test(str);
					  if (/^\[(.*?)\]$/.test(str)) {
					    return str.replace(/^\[/, '')
					      .replace(/\]$/, '')
					      .replace(/},{/g, '}\n{')
					      .split(/\n/)
					      .map(function (s) { return isJSON(s); })
					      .reduce(function (prev, curr) { return !!curr; });
					  }
					  return false;
					}
					if(isJSON(value)){
						callback()
					}
					else{
						callback(new Error("请输入json格式数据"))
					}
				}
				else{
					callback()
				}
			};
			return {
				show: false,
				form: new Form(),
				querySearch:function(queryString, cb){
			        cb([{value:'查询所有记录'},
			        	{value:'根据条件查询记录'},
			        	{value:'根据条件分页查询记录'},
			        	{value:'新建记录'},
			        	{value:'根据多个id删除记录'},
			        	{value:'更新记录'},
			        	{value:'根据id查询记录'}
			        ]);
				},
				rules: {
					param: [{
							validator: _.debounce(param,500),
							trigger: 'change'
					}]
				}
			}
		},
		methods: {
			getPage() {
				this.form = new Form();
				if(this.$refs.form) {
					this.$refs.form.resetFields();
				}
				this.show = true
			},
			submit() {
				this.$refs['form'].validate((valid) => {
					if(valid) {
						this.show = false;
						this.form.menuId = this.$parent.selectNode.id
						create(this.form).then((res) => {
							this.$parent.queryData();
						});
					} else {
						return false;
					}
				});
			}
		}
	}
</script>

<style scoped>
.block{
	display: block;
}
</style>


