<template>
	<el-dialog title="测试API" :visible.sync="show" size="full" id="test">
		<el-form :model="form" :rules="rules" ref="form">
			<el-col class="testTitle">
				<span class="type">{{form.type}}</span>
				<span class="url">{{form.url}}</span>
				<span class="name">{{form.name}}</span>
			</el-col>
			<el-form-item label="输入参数" prop="param">
				<el-input type="textarea" v-model="form.param" placeholder="输入参数"></el-input>
			</el-form-item>
			<el-col>
				<label class="el-form-item__label">返回结果</label>
			</el-col>
			<el-col class="code" v-if="form.response" :style="{height:height}"  v-loading="loading">
			 	<codemirror v-model="form.response" :options="editorOptions"></codemirror>
			</el-col>
		</el-form>
		<span slot="footer" class="dialog-footer">
		    <el-button class="btnTest" type="primary" @click="submit()" :disabled="loading">测试</el-button>
		</span>
	</el-dialog>
</template>

<script>
	import {baseUrl} from '@/config/env'
	import {
		queryArea,
		create
	} from "./service";
	
	class Form {
		constructor(data={}) {
			this.name = data.name;
			this.url = data.url;
			this.param = data.param;
			this.type = data.type;
			this.response = data.response;
		}
	}

	export default {
		computed:{
			height :function(){
				return this.$.clientHeight() - 380 + 'px'
			}
		},
		data() {
			const param = (rule, value, callback) => {
				if(value) {
					function isJSON (str) {
					  str = str.replace(/\s/g, '').replace(/\n|\r/, '');
					  if (/^\{(.*?)\}$/.test(str))
					    return /"(.*?)":(.*?)/g.test(str);
					  if (/^\[(.*?)\]$/.test(str)) {
					    return str.replace(/^\[/, '')
					      .replace(/\]$/, '')
					      .replace(/},{/g, '}\n{')
					      .split(/\n/)
					      .map(function (s) { return isJSON(s); })
					      .reduce(function (prev, curr) { return !!curr; });
					  }
					  return false;
					}
					if(isJSON(value)){
						callback()
					}
					else{
						callback(new Error("请输入json格式数据"))
					}
				}
				else{
					callback()
				}
			};
			return {
				show: false,
				form: new Form(),
				rules: {
					param: [{
							validator: _.debounce(param,500),
							trigger: 'change'
					}]
				},
				loading:false,
				editorOptions:{
					mode: 'text/javascript'
				}
			}
		},
		methods: {
			getPage(data) {
				this.form = new Form(data);
				if(this.$refs.form) {
					this.$refs.form.resetFields();
				}
				this.show = true
			},
			submit() {
				this.loading = true
				this.$refs['form'].validate((valid) => {
					if(valid) {
						this.ajax({
						     url:this.form.url,
						     type:this.form.type,
						     data:this.form.param,
						     done:(response) => {
						     	this.loading = false
						     	this.form.response = response.responseText
						     }
						});
					} else {
						this.loading = false
						return false;
					}
				});
			},
			ajax(obj){
	            obj=obj||{};
	            obj.type=(obj.type||'GET').toUpperCase();
	            obj.dataType=obj.dataType||'json';
	            obj.data=obj.data||"";
	            obj.url = baseUrl + obj.url
	            var xhr=new XMLHttpRequest();
	            xhr.onreadystatechange=function(){
	                if(xhr.readyState==4){
	                    obj.done(xhr)
		            }
				}
	            if(obj.type=='GET'){
	                xhr.open('GET',obj.url+format(obj.data),true);
	                xhr.send(null);
	            }
	            else if(obj.type=='POST'){
	                xhr.open('POST',obj.url,true);
	                //设置请求头，以表单形式提交数据
	                xhr.setRequestHeader('Content-Type','application/json');
	                xhr.send(obj.data);
	            }
	            function format(obj){
	            	let str = ""
	            	if(obj){
	            		obj = JSON.parse(obj)
	            		str += "?"
		            	for(let x in obj){
		            		str += (x + "=" + obj[x] + "&")
		            	}
		            	str = str.slice(0,str.length-1)
	            	}
	            	return str
	            }
	        }
		}
	}
</script>

<style>
#test .el-dialog--full{
	width: 50%;
}
#test .testTitle{
	margin-bottom:22px
}
#test .testTitle .type{
	padding:5px 20px;
	background: #61affe;
	border-radius: 3px;
	font-size:16px;
	color:#FFF
}
#test .testTitle .url,#test .testTitle .name{
	font-size:16px;
	margin-left:30px;
	letter-spacing: 1px;
	font-family: 'Source Code Pro', monospace;
	font-weight: 600;
}
#test .btnTest{
	width:100%
}
#test .el-dialog__body{
	padding:30px 20px;
}
#test .code{
	border-radius:4px;
	border:1px solid #bfcbd9;
	margin-bottom:22px;
}
#test .CodeMirror{
	height:100%;
}
</style>


