<template>
  <div id="app">
		<header>
			<div class="logo">
				<img src="~assets/images/index/logo2.png"/>
				<span>API测试工具</span>
			</div>
			<div class="menu">
				<ul>
					<li v-for="value in headMenuList" :key="value.id" @click="getChildMenu(value)">
						<a :class="{'active':parentMenu.id == value.id}">
							<div class="menuIcon"><img :src="require('assets/images/index/' + value.icon)"/></div>
							<span>{{value.name}}</span>
						</a>
					</li>
				</ul>
			</div>
			<div class="loginBox">
				
			</div>
    </header>
		<div class="app_container">
			<div class="navbar">
					<mes-tree :tree-data="treeData" @tree-click="treeClick"></mes-tree>
			</div>
			<div class="content">
								<div class="mes-tool">
									<el-button type="primary" @click="create_getPage" :disabled="!selectNode||(selectNode&&selectNode.isRoot)">新建<i class="el-icon-upload el-icon--right"></i>
									</el-button>
									<el-button type="danger" @click="multiDelete" :disabled="mutiDeleteData.length == 0">删除<i class="el-icon-upload el-icon--right"></i>
									</el-button>
									<el-input placeholder="请输入搜索信息" icon="search" v-model="searchData" :on-icon-click="search" @keyup.enter.native="search">
									</el-input>
								</div>
								<div class="mes-table" v-if="table.data">
									<mes-table :table-data="table.data"
										:table-option="table.option"
										:table-loading="table.loading"
										:total="table.pageTotal"
										:pageSize="table.pageSize"
										:pageChange="pageChange"
										:sizeChange="sizeChange"
										:pageNum="table.pageNum"
										:showCheckBox="true"
										@selectChange="selectChange">
										<el-table-column fixed="right" label="操作" width="150" v-if="table.data.length>0" slot="operationTool">
											<template scope="scope">
												<el-button @click.native.prevent="edit_getPage(scope.row)" type="text" size="small">
													编辑
												</el-button>
												<el-button @click.native.prevent="test_getPage(scope.row)" type="text" size="small">
													测试
												</el-button>
												<el-button @click.native.prevent="delete_getPage(scope.row)" type="text" size="small">
													删除
												</el-button>
											</template>
										</el-table-column>
									</mes-table>
								</div>
						<createForm ref="create_form"></createForm>
						<editForm ref="edit_form"></editForm>
						<testForm ref="test_form"></testForm>
			</div>
		</div>
  </div>
</template>

<script>
	import {
		queryMenu,
		queryChildMenu,
		queryData,
		deleting
	} from "./service";

	import createForm from "./create"
	import editForm from "./edit"
	import testForm from "./test"
	
export default {
		components: {
			createForm,
			editForm,
			testForm
		},
		created() {
			this.init();
		},
  data() {
      return {
      	headMenuList:[],
      	treeData:[],
      	parentMenu:{},
				selectNode: {},
				mutiDeleteData: [],
      	table: {
					option: [{
						prop: "name",
						label: "名称",
						width:250
					},{
						prop: "url",
						label: "url地址",
						width:250
					},{
						prop: "type",
						label: "提交方式",
						width:120
					},{
						prop: "param",
						label: "默认参数"
					}],
					data: false,
					loading: true,
					pageSize: 10,
					pageNum: 1
				},
				searchData: ''
      };
    },
    methods:{
			init() {
				this.getHeadMenu()
				this.getTree()
				this.queryData()
			},
			async getHeadMenu() {
				let res = await queryMenu({parentId:"0"});
				if(res.status == "success") {
					this.headMenuList = res.content.sort(function(a,b){
						return a.itemOrder - b.itemOrder
					});
				}
				else {
					this.headMenuList = [];
				}
				this.parentMenu = this.headMenuList[0]
				this.getTree()
			},
			getChildMenu(data){
				this.parentMenu = data
				this.getTree()
			},
			async getTree() {
				let data = {
					condition: {
						parentId: this.parentMenu.id||"1000"
					},
					pageSize: 1000,
					pageNum: 1
				}
				let res = await queryChildMenu(data);
				let menu = res.content.rows.concat(this.$.shallowCopy(this.parentMenu))
				if(res.status == "success") {
					this.treeData = this.$.transformTree(menu, {
					})
				} else {
					this.treeData = []
				}
				this.selectNode = null
			},
			treeClick: function(node) {
				this.selectNode = node
				this.queryData()
			},
			async queryData() {
				this.table.loading = true;
				let data = {
					condition: {
						menuId: this.selectNode ? this.selectNode.id : "",
						search:this.searchData
					},
					pageSize: this.table.pageSize,
					pageNum: this.table.pageNum
				}
				let res = await queryData(data);
				if(res.status == "success") {
					this.table.data = res.content.rows || [];
					this.table.pageTotal = res.content.total;
				} else {
					this.table.data = [];
					this.table.pageTotal = 0;
				}
				this.table.loading = false;
			},
			
			create_getPage() {
				this.$refs.create_form.getPage()
			},
			edit_getPage(data){
				this.$refs.edit_form.getPage(data)
			},
			test_getPage(data){
				this.$refs.test_form.getPage(data)
			},
			delete_getPage(data) {
				this.$.delete('此操作将永久删除该接口, 是否继续?',() => {
					deleting({
						ids: data.id
					}).then((res) => {
						this.queryData()
					})
				})
			},
			
			search() {
				this.queryData();
			},
			pageChange(currentPage) {
				this.table.pageNum = currentPage
				this.queryData();
			},
			sizeChange(size) {
				this.table.pageSize = size
				this.queryData();
			},
			selectChange(data){
				this.mutiDeleteData = data
			},
			multiDelete() {
				this.$.delete('此操作将永久删除所选接口, 是否继续?',() => {
					deleting({
						ids: this.mutiDeleteData
					}).then((res) => {
						this.queryData()
					})
				})
			}
    }
}
</script>

<style>
	*{
		box-sizing: border-box;
	}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,button,textarea,p,blockquote,th,td { margin:0; padding:0; }
body {color:#414a59; font-size:14px; font-family: Verdana, Arial, Helvetica, sans-serif;
	overflow-x: hidden;
	}
	label{
	font:400 14px "microsoft yahei";
	font-weight:400}
td,th,caption { font-size:14px; }
h1, h2, h3, h4, h5, h6 { font-weight:normal; font-size:100%; }
address, caption, cite, code, dfn, em, strong, th, var { font-style:normal; font-weight:normal;}
a { color:#555; text-decoration:none; }
a:hover { text-decoration:none; }
a:focus{outline: none;text-decoration:none;}
img { border:none; }
ol,ul,li { list-style:none; }
input, textarea, select, button { font:14px Verdana,Helvetica,Arial,sans-serif; }
table { border-collapse:collapse;table-layout:fixed;white-space: nowrap;}
td{overflow: hidden;text-overflow: ellipsis;}
html,body {height:100%;width:100%;overflow: auto;} 
#app{
  position:absolute;
  min-width:1336px;
  top:0;
  bottom:0;
  left:0px;
  right:0px;
  overflow: hidden;
}
.app_container{
	position: absolute;
  top:80px;
  bottom:0;
  left:0;
  right:0;
}
.navbar{
		position: absolute;
		top:0;
		left:0;
		width:300px;
		bottom:0;
		overflow: hidden;
		transition: all linear 0.1s;
	}
.content{
	position: absolute;
  top:0;
  bottom:0;
  left:300px;
  right:0;
}
.mes-tree {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	padding: 20px;
}
	
	
.mes-tool {
	position: absolute;
	top: 0;
	height: 56px;
	left: 0;
	right: 0;
	padding: 19px 20px 0;
}
.mes-tool .el-input{
	width:250px;
	float:right;
}
.mes-table {
	position: absolute;
	top: 60px;
	left: 0;
	right: 0;
	bottom: 0;
	padding: 11px 20px;
}


/*表单*/

.el-dialog__header{
	padding: 18px 20px;
	border-bottom: 1px solid rgb(210,210,210);
}
.el-dialog__title{
	font-weight: normal;
	letter-spacing: 1px;
}
.el-dialog__body{
	padding: 30px 70px 0;
}
.el-form-item__label{
	padding:11px 30px 11px 0;
	color: rgb(97,102,110);
}
.el-select{
	width: 100%;
}
.mes-upload{
	margin-top:22px;
}
.submit_button{
	padding-top:30px
}
.submit_button .el-form-item__content{
	text-align: right;
}

/*mask*/
.el-loading-mask.mes{
	background-color: rgba(255,255,255,.15);
}

header{
	width:100%;
	height:80px;
	box-shadow: 0px 1px 10px #888888;
	position: relative;
	background-color:rgb(57,141,238);
	color:#FFF;
}
header .logo{
	float:left;
	width: 240px;
	height: 100%;
	overflow: hidden;
	font:400 18px "CenturyGothic";
	color:#FFF;
	padding:25px;
	letter-spacing: 2px;
}
header .logo img{
	float:left;
	margin-right:5px;
}
header .logo span{
	float:left;
	line-height: 30px;
}
header .menu{
	height: 100%;
	position: absolute;
	left:240px;
	right:310px;
	top:0;
	bottom:0;
}
header .menu ul{
	padding:0 10px 10px 10px;
    float: left;
    position: absolute;
	height: 80px;
	overflow: hidden;
	transition: all linear 0.3s;
}
header .menu ul li{
	position:relative;
	float:left;
    display: block;
	height: 80px;
	width:100px;
	font:400 14px "microsoft yahei";
	text-align: center;
	cursor:pointer;
}
header .menu ul li a{
	display: inline-block;
	height: 100%;
	width: 100%;
	border-top:4px solid transparent
}
header .menu ul li a span{
	display: inline-block;
	width:100%;
	position:absolute;
	bottom:12px;
	left:0;
	text-align: center;
	color:rgb(225,225,228)
}
header .menu ul li a .menuIcon{
	height:42px;
	width:96px;
	margin:10px auto;
	overflow: hidden;
}
header .menu ul li a .menuIcon img{
	position: relative;
}
header .menu ul li a.active{
	background:rgb(49,120,203);
	border-color:rgb(49,195,145)
}
header .menu ul li a.active img{
	top:-42px
}
header .menu ul li a.active span{
	color:rgb(255,255,255)
}
header .loginBox{
	height:100%;
	color:#000;
	position:absolute;
	right:0;
	padding:20px 50px
}
</style>



