import fetch from '@/config/fetch'

export const queryMenu = (data) => fetch('/menu/byCondition', data, "POST");
export const queryChildMenu = (data) => fetch('/menu/byPage', data, "POST");
export const queryData = (data) => fetch('/menurestapi/byPage', data, "POST");

//export const queryApi = (data) => fetch('/menurestapi/byCondition', data, "POST");
export const create = (data) => fetch('/menurestapi/creating', data, "POST",true);
export const edit = (data) => fetch('/menurestapi/updating', data, "POST",true);
export const deleting = (data) => fetch('/menurestapi/deleting', data, "GET",true);















